// 
@import "./function";

// 
//  CSS 
$theme-primary: #2196f3 !default; //  $blue-500 #2196f3
$theme-accent: #e040fb !default; //  $purple-a-200 #e040fb
$theme-background: #fff !default; // 

// 
$theme-primary-light: #90caf9 !default; //  $blue-200
$theme-accent-light: #ce93d8 !default; //  $purple-200

// 
$theme-primary-dark: #1976d2 !default; //  $blue-700
$theme-accent-dark: #d500f9 !default; //  $purple-a-400

//  light or dark
$theme-primary-tone: theme-light-or-dark($theme-primary);
$theme-accent-tone: theme-light-or-dark($theme-accent);
$theme-background-tone: theme-light-or-dark($theme-background);

// 
//  disabled readonly 
$theme-text-colors: (
  dark: (
    primary: rgba(0, 0, 0, .87),
    secondary: rgba(0, 0, 0, .54),
    hint: rgba(0, 0, 0, .38),
    disabled: rgba(0, 0, 0, .38)
  ),
  light: (
    primary: #ffffff,
    secondary: rgba(255, 255, 255, .7),
    hint: rgba(255, 255, 255, .5),
    disabled: rgba(255, 255, 255, .5)
  )
);

// 
$theme-property-values: (
  // 
  primary: $theme-primary,
  accent: $theme-accent,
  background: $theme-background,

  // 
  primary-light: $theme-primary-light,
  accent-light: $theme-accent-light,
  // 
  primary-dark: $theme-primary-dark, //  $blue-700
  accent-dark: $theme-accent-dark, //  $purple-a-400

  //  background-color border-color  color 
  //  map-get($theme-text-colors, $theme-primary-tone)  light 
  //  $theme-primary  primary secondary hint  disabled 
  text-primary-on-primary: map-get(map-get($theme-text-colors, $theme-primary-tone), primary),
  text-secondary-on-primary: map-get(map-get($theme-text-colors, $theme-primary-tone), secondary),
  text-hint-on-primary: map-get(map-get($theme-text-colors, $theme-primary-tone), hint),
  text-disabled-on-primary: map-get(map-get($theme-text-colors, $theme-primary-tone), disabled),

  //  $theme-accent  primary secondary hint  disabled 
  text-primary-on-accent: map-get(map-get($theme-text-colors, $theme-accent-tone), primary),
  text-secondary-on-accent: map-get(map-get($theme-text-colors, $theme-accent-tone), secondary),
  text-hint-on-accent: map-get(map-get($theme-text-colors, $theme-accent-tone), hint),
  text-disabled-on-accent: map-get(map-get($theme-text-colors, $theme-accent-tone), disabled),

  //  $theme-background  primary secondary hint  disabled 
  text-primary-on-background: map-get(map-get($theme-text-colors, $theme-background-tone), primary),
  text-secondary-on-background: map-get(map-get($theme-text-colors, $theme-background-tone), secondary),
  text-hint-on-background: map-get(map-get($theme-text-colors, $theme-background-tone), hint),
  text-disabled-on-background: map-get(map-get($theme-text-colors, $theme-background-tone), disabled),

  //  primary secondary hint  disabled 
  text-primary-on-light: map-get(map-get($theme-text-colors, dark), primary),
  text-secondary-on-light: map-get(map-get($theme-text-colors, dark), secondary),
  text-hint-on-light: map-get(map-get($theme-text-colors, dark), hint),
  text-disabled-on-light: map-get(map-get($theme-text-colors, dark), disabled),

  //  primary secondary hint  disabled 
  text-primary-on-dark: map-get(map-get($theme-text-colors, light), primary),
  text-secondary-on-dark: map-get(map-get($theme-text-colors, light), secondary),
  text-hint-on-dark: map-get(map-get($theme-text-colors, light), hint),
  text-disabled-on-dark: map-get(map-get($theme-text-colors, light), disabled)
);

//  material design  CSS 
//  success  warning  error 
// 
//  $theme-primary $theme-accent success  info warning  error
// 
$theme-assist-colors: (
  success: #4caf50, // $green-500
  info: #00bcd4, // $cyan-500
  warning: #ff9800, // $orange-500
  error: #f44336, // $red-500
) !default;

// 
$theme-assist-colors-light: (
  success: rgba(map-get($theme-assist-colors, success), .54),
  info: rgba(map-get($theme-assist-colors, info), .54),
  warning: rgba(map-get($theme-assist-colors, warning), .54),
  error: rgba(map-get($theme-assist-colors, error), .54),
) !default;

// 
$white: #ffffff;
$grey-50: #fafafa;
$grey-100: #f5f5f5; // grey-lightest
$grey-200: #eeeeee;
$grey-300: #e0e0e0; // grey-lighter
$grey-400: #bdbdbd;
$grey-500: #9e9e9e; // grey-light
$grey-600: #757575;
$grey-700: #616161; // grey
$grey-800: #424242;
$grey-900: #212121; // grey-dark
$black: #000000;

