/// Grid mixin

@import "../../base/variables/options";
@import "../../responsive/variable";

//  row 
//  flex 
@mixin make-row($gutter: $grid-gutter-width) {
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  margin-left: ($gutter / -2);
  margin-right: ($gutter / -2);

  * {
    box-sizing: border-box;
  }
}

// 
@mixin make-col($size, $columns: $grid-columns) {
  flex: 0 0 percentage($size / $columns);
  //  IE10+  Firefox 
  //  Chrome  Safari 
  max-width: percentage($size / $columns);
}

// offset  margin-left 
@mixin make-col-offset($size, $columns: $grid-columns) {
  margin-left: percentage($size / $columns);
}

//  gird framework mixin
@mixin make-grid-columns($columns: $grid-columns, $gutter: $grid-gutter-width, $breakpoints: $responsive-breakpoints) {
  // 
  %grid-column {
    position: relative;
    width: 100%;
    min-height: 1px; // 
    padding-left: ($gutter / 2);
    padding-right: ($gutter / 2);
  }

  // fixme 
  @if $enable-responsive {
    @each $breakpoint in map-keys($breakpoints) {
      // -md
      $infix: breakpoint-infix($breakpoint, $breakpoints);

      //  1  $columns
      @for $i from 1 through $columns {
        .col#{$infix}-#{$i} {
          @extend %grid-column;
        }
      }
      .col#{$infix} ,
      .col#{$infix}-auto {
        @extend %grid-column;
      }

      @include media-breakpoint-up($breakpoint, $breakpoints) {
        //  Flexbox  .col-md 
        .col#{$infix} {
          flex-basis: 0;
          flex-grow: 1;
          max-width: 100%;
        }
        .col#{$infix}-auto {
          flex: 0 0 auto;
          width: auto;
          max-width: none;
        }

        @for $i from 1 through $columns {
          .col#{$infix}-#{$i} {
            @include make-col($i, $columns);
          }
        }

        //  col 
        @for $i from 1 through $columns {
          .order#{$infix}-#{$i} {
            order: $i;
          }
        }

        //  offset  0  $columns - 1
        @for $i from 0 through ($columns - 1) {
          @if not ($infix == "" and $i == 0) { // 0 .offset-xs-0
            .offset#{$infix}-#{$i} {
              @include make-col-offset($i, $columns)
            }
          }
        }
      }
    }
  } @else { // 
    @for $i from 1 through $columns {
      .col-#{$i} {
        @extend %grid-column;
      }
    }
    .col,
    .col-auto {
      @extend %grid-column;
    }
    .col {
      flex-basis: 0;
      flex-grow: 1;
      max-width: 100%;
    }
    .col-auto {
      flex: 0 0 auto;
      width: auto;
      max-width: none;
    }

    @for $i from 1 through $columns {
      .col-#{$i} {
        @include make-col($i, $columns);
      }
    }

    //  col 
    @for $i from 1 through $columns {
      .order-#{$i} {
        order: $i;
      }
    }

    //  offset  0  $columns - 1
    @for $i from 0 through ($columns - 1) {
      @if not ($i == 0) { // 0 .offset-xs-0
        .offset-#{$i} {
          @include make-col-offset($i, $columns)
        }
      }
    }
  }
}
